'use client'

import createProduct from "./createProduct"
import { useEffect, useState } from "react"

export default function FormProduct(){
    const [storage,setStorage]=useState('')
    const param=useSearchParams()
    const [value,setValue]=useState({user:params.getItem('user'),title:'',description:'',category:'',price:0})
    const validator=(data:any)=>{
        return createProduct(data)
    }
    useEffect(  ()=>{
        setStorage(localStorage.getItem('user')!)
        setValue({...value,user:storage})
    },[])
    return <form>
        <p>Title</p>
        <input value={value.title} onChange={(e)=>setValue({...value,title:e.target.value})} type="text" />
        <p>Description</p>
        <input value={value.description} onChange={(e)=>setValue({...value,description:e.target.value})} type="text" />
        <select value={value.category} onChange={(e)=>setValue({...value,category:e.target.value})} >
            <option value="Techonology">Techonology</option>
            <option value="Beauty">Beauty</option>
            <option value="Games">Games</option>
            <option value="Toys">Toys</option>
        </select>
        <p>Price in $</p>
        <input value={value.price} onChange={(e)=>setValue({...value,price:Number(e.target.value)})} type="number" />
        <button onClick={(e)=>{e.preventDefault();validator(value)}}>Create</button>
    </form>
}